    const sm3Info = ref("");

    const check = ref(false); //
    const equal = ref(false); //
    const send = ref(null); //
    const requestNumber = ref(""); //
    const sm2Type = ref(1);
    const sm2EncryptText = ref("");
    const sm2DecryptText = ref("");
    const sm2PrivateKey = ref("");
    const sm2BackInfo = ref(""); //sm2
    const sm2BackInfo1 = ref("");
    const sm3EncryptText = ref("");
    const sm3Message = ref("");
    const sm3Valid = ref("");
    const sm3BackInfo = ref(""); //sm3
    const sm3BackInfo1 = ref("");

    // const policeInfo = ref({});
    // const houseManagementInfo = ref({});
    const departmentInfo = ref({});

    const currentId = route.params.requestId; //(id,)

    const getUserInfoBackChange = computed(() => {
      return userInfo.value;
    });

    onMounted(() => {
      watch(getUserInfoBackChange, (newVal, oldVal) => {
        console.log(userInfo.value);
      });
    });

    const getData = async () => {
      try {
        const { data: requestInfo, error } = await supabase
          .from("oh")
          .select("*")
          .eq("id", currentId);

        if (error) throw error;
        console.log(requestInfo);
        data.value = requestInfo;
        requestNumber.value = requestInfo[0].requestNumber;
        // d.value.push(requestInfo);

        sm2EncryptText.value = requestInfo[0].sm2Info;

        dataLoad.value = true;
        console.log(data.value);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };
    getData();

    // Edit mode
    const edit = ref(null);

    const editMode = () => {
      edit.value = !edit.value;
    };

    const update = async (currentId, status) => {
      console.log(currentId);
      try {
        const { error } = await supabase
          .from("oh")
          .update({
            status: status,
          })
          .eq("id", currentId);
        if (error) throw error;
        statusMsg.value = "";
        setTimeout(() => {
          statusMsg.value = false;
          router.push({ name: "View-Oh-Info" });
        }, 3000);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 3000);
      }
    };

    const sm2Decrypt = (encrypted, privateKey, cipherMode) => {
      return sm2.doDecrypt(encrypted, privateKey, cipherMode);
    };

    const sm3Encrypt = async (info) => {
      return sm3(info);
    };

    const decrypt = async (d) => {
      // 
      const { data: keys, error } = await supabase.from("crypto_key").select("*");

      console.log(keys);
      console.log(data.value[0].owner);

      //
      sm2PrivateKey.value = keys[0].private_key;
      // 
      sm2DecryptText.value = sm2Decrypt(d, sm2PrivateKey.value, sm2Type.value);
    };

    //DO
    const sm3Check = async () => {
      try {
        await decrypt(sm2EncryptText.value);
        console.log(sm2DecryptText.value);

        sm3Message.value = await sm3Encrypt(sm2DecryptText.value);
        console.log(sm3Message.value);

        check.value = true;

        if (sm3Message.value === sm3EncryptText.value) {
          equal.value = true;
          statusMsg.value = "";
          // setTimeout(() => {
          //   statusMsg.value = false;
          // }, 3000);

          // }
        } else {
          throw error;
        }
      } catch (error) {
        errorMsg.value = "";
        setTimeout(() => {
          errorMsg.value = false;
        }, 3000);
      }
    };
    //
    //departmentInfo,
    const sendDataFromOhToDepartment = async (department) => {
      try {
        if (department === "police_request_info") {
          departmentInfo.value["id_number"] = data.value[0].requestInfo.idNumber;
          departmentInfo.value["name"] = data.value[0].requestInfo.owner;
          departmentInfo.value["phone_number"] = data.value[0].requestInfo.phoneNumber;
        } else if (department === "house_management_request_info") {
          departmentInfo.value["house_number"] = data.value[0].requestInfo.houseNumber;
        }
        const { data: sendInfo, error } = await supabase.from(department).insert([
          {
            request_number: requestNumber.value,
            oh_info: JSON.stringify(departmentInfo.value),
            id_number: data.value[0].requestInfo.idNumber,
          },
        ]);
        if (error) throw error;
        departmentInfo.value = {};
        console.log(sendInfo[0]);
        if (department === "police_request_info")
          statusMsg.value = "";
        else if (department === "house_management_request_info")
          statusMsg.value = "";
        setInterval(() => {
          statusMsg.value = false;
        }, 5000);
      } catch (error) {
        errorMsg.value = `Error: ${error.message}`;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };

    const getDataFromDepartment = async (department, id) => {
      try {
        const { data: userInfoBack, error } = await supabase
          .from(department)
          .select("*")
          .eq("id_number", id);

        userInfo.value = userInfoBack;
        send.value = true;
        //sm2,sm3,
        sm2EncryptText.value = userInfoBack[0].sm2_info;
        sm3EncryptText.value = userInfoBack[0].sm3_info;
        //(SM2, SM3)
        if (department === "police_request_info") {
          sm2BackInfo.value = userInfoBack[0].sm2_info;
          sm3BackInfo.value = userInfoBack[0].sm3_info;
        } else if (department === "house_management_request_info") {
          sm2BackInfo1.value = userInfoBack[0].sm2_info;
          sm3BackInfo1.value = userInfoBack[0].sm3_info;
        }

        if (error) throw error;
        statusMsg.value = `${department}`;
        setTimeout(() => {
          statusMsg.value = false;
        }, 5000);
      } catch (error) {
        errorMsg.value = error.message;
        setTimeout(() => {
          errorMsg.value = false;
        }, 5000);
      }
    };

    //
    const verifyInfoBetweenDepartMentAndUser = async () => {
      const v = [...userInfo.value];
      console.log(v);
      for (let key in v) {
        let t = v[key];
        // for (let index in t[0]) {
        //index, t[0][index]
        // console.log(`${index}: ${t[0][index]}`);
        // if (index !== "id")
        //   if (index !== "created_at") valifyInfo.value[index] = t[0][index];
        if (t[0]["id_number"]) valifyInfo.value["idNumber"] = t[0]["id_number"];
        if (t[0]["name"]) valifyInfo.value["owner"] = t[0]["name"];
        if (t[0]["phone_number"]) valifyInfo.value["phoneNumber"] = t[0]["phone_number"];
        if (t[0]["house_number"]) valifyInfo.value["houseNumber"] = t[0]["house_number"];
        console.log(valifyInfo.value);
        // }
      }
      await sm3Check(valifyInfo);
    };

    return {
